import { useState } from "react";

const AddCommentForm = ({ articleInfo, setArticleInfo }) => {
    const [username, setUserName] = useState('');
    const [commentText, setCommentText] = useState('');

    const submitComment = async () => {
        if (username.length === 0 || commentText.length === 0) {
            alert('username and comment text fields are required!');
            return;
        }
        const res = await fetch(
            `/api/article/${articleInfo.name}/add-comment`,
            {
                method: 'post',
                headers: { 'Content-Type': 'application/json'},
                body: JSON.stringify({ username, text: commentText })
            }
        );
        const body = await res.json();
        setArticleInfo({comments: [articleInfo.comments, ...body.comments]}); //TODO: may be just put res.body here? so data is updated. then the articleInfo variable will not bee needed. only name 
        setUserName('');
        setCommentText('');
    }

    return (
        <div id="add-comment-form">
            <h3>Add a comment:</h3>
            <label>
                Name:
                <input
                    type="text"
                    value={username}
                    onChange={e => setUserName(e.target.value)}/>
            </label>
            <label>
                Comment:
                <textarea
                    rows="4"
                    cols="50"
                    value={commentText}
                    onChange={event => setCommentText(event.target.value)} />
                <button onClick={submitComment}>Add Comment</button>
            </label>
        </div>
    );
}

export default AddCommentForm;
