import { useState, useEffect } from 'react';
import ArticleList from '../components/ArticleList';
import ArticleComments from '../components/ArticleComments';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';
import ErrorPage from './ErrorPage';
import articleContent from './article-content';

const ArticlePage = ({ match }) => {
    const { name } = match.params
    const article = articleContent.find(article => article.name === name);
    
    const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: []});

    useEffect(() => {
        // console.log('name: ', name)
        const fetchData = async () => {
            const res = await fetch(`/api/articles/${name}`);
            const body = await res.json();
            console.log(body);
            setArticleInfo(body);
        };
        fetchData()
    }, [name]);

    if (!article) return <ErrorPage />;

    const otherArticles = articleContent.filter(article => article.name !== name);
    console.log('name: ', name)
    return (
        <>
            <h1>{article.title}</h1>
            <UpvotesSection articleInfo={articleInfo} setArticleInfo={setArticleInfo} />
            {
                article.content.map((paragraph, i) => (
                <p key={i}>{paragraph}</p>
                ))
            }
            <ArticleComments comments={articleInfo.comments} />
            <AddCommentForm articleInfo={articleInfo} setArticleInfo={setArticleInfo}/>
            <h3>Other Articles:</h3>
            <ArticleList articles={otherArticles} />
        </>
    );
};

export default ArticlePage;
